/**Scroll Bar**/
aside form .input-block textarea::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

aside form .input-block textarea::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

aside form .input-block textarea::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #555;
}

/**Scroll Bar **/

aside {
    width: 320px;
    margin-bottom: 30px;
    background: #fff;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 1.0);
    border-radius: 10px;
    padding: 30px 20px;
    position: fixed;
}

aside strong {
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}

aside form {
    margin-top: 30px;
}

aside form .input-block {
    margin-top: 20px;
}

aside form .input-block label {
    color: #acacac;
    font-size: 14px;
    font-weight: bold;
    display: block;

}

aside form .input-block input {
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0;
    border-bottom: 1px solid #eee;
}

aside form .input-block textarea {
    margin-top: 7px;
    width: 100%;
    height: 200px;
    font-size: 14px;
    color: #666;
    border-bottom: 1px solid #eee;
    background: #fff;
    resize: none;

}

aside form button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #a0a7e9;
    border-radius: 10px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    cursor: pointer;
}
